import { Injectable } from '@angular/core';

@Injectable()
export class SessionService {

  constructor() { }

  public logged=false;


  public login() {
    this.logged = true;
  }


  public logout() {
    this.logged = false;
  }

  public isLogged() {
    return this.logged;
  }



}



